import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { Router, NavigationExtras } from '@angular/router';

import { AuthService } from '../../_service/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  recaptchaV2Sitekey = '6LdYirAUAAAAAAGvdmKaDPxEqWgFO9FHl68hawVH';
  recaptchaV2secret = '6LdYirAUAAAAAGnZ5Bx5AD50LiVNq8PZU9LOVVKI';

  signinForm = new FormGroup({
    emailId: new FormControl('', [Validators.required, Validators.email]),
    password: new FormControl('', [Validators.required]),
    // captcha: new FormControl()
  });

  validationMsg = {
    emailId: {
      required: 'Email cannot be left blank',
      email: 'Please enter a valid email ID'
    },
    password: {
      required: 'Password cannot be left blank',
    }
  };

  passwordVisible = false;
  serverError: any = '';

  passwordError;
  passwordCtl;

  constructor(private titleService: Title, public router: Router, private spinnerService: Ng4LoadingSpinnerService, public authService: AuthService) { }

  ngOnInit() {
    this.titleService.setTitle('Sign In');
    this.passwordCtl = this.signinForm.get('password');
  }

  ngDoChange() {
    this.passwordError = this.getErrorMessage('password');
  }

  getErrorMessage(formControlName: string) {
    const formControl = this.signinForm.get(formControlName);
    let message = '';

    if (formControl.errors) {
      Object.keys(formControl.errors).forEach(key => {
        message = this.validationMsg[formControlName][key];
      });
    }

    return message;
  }

  showPassword() {
    this.passwordVisible = true;
  }

  hidePassword() {
    this.passwordVisible = false;
  }

  login() {
    this.serverError = '';

    const form = this.signinForm;
    if (form.valid) {
      let username = form.get('emailId').value;
      username = username.trim();
      const password = form.get('password').value;

      this.spinnerService.show();

      this.authService.login(username, password).subscribe(
        (res) => {

          this.spinnerService.hide();

          if (this.authService.isLoggedIn) {
            switch (this.authService.userRole) {
              case 'vessel_operator':
              case 'cargo_owner':
                this.serverError = 'User role not authorized. Please check your email for instructions to login.';
                break;
              case 'client_super_admin':
              case 'diabos_super_admin':
              case 'broker':
              case 'assistant':
              case 'diabos_operations_head':
              case 'diabos_operator':
              case 'client_operator':
              case 'client_product_super_admin':
              case 'client_product_operator':
              case '':
                this.serverError = '';
                const redirectUrl = this.authService.redirectUrl;
                if (redirectUrl) {
                  this.authService.redirectUrl = '';
                  this.router.navigate([redirectUrl]);
                } else {
                  this.router.navigate(['/diabos-apps']);
                }

                break;
              default:
                this.serverError = 'Invalid user role';
                break;
            }
          } else {
            this.serverError = res.error || 'Failed to login';
          }
        }
      );
    }
  }
}
