import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { MatDialog } from '@angular/material';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { Router, ActivatedRoute } from '@angular/router';

import { SimpleDialogComponent } from '../../shared/simple-dialog/simple-dialog.component';
import { ResendValidationLinkService } from '../../_service/resend-validation-link.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-resend-validation-link',
  templateUrl: './resend-validation-link.component.html',
  styleUrls: ['../login/login.component.scss', '../signup/signup.component.scss', '../forgot-password/forgot-password.component.scss', '../reset-password/reset-password.component.scss', './resend-validation-link.component.scss']
})
export class ResendValidationLinkComponent implements OnInit {
  serverError: string;
  hintText = 'Press the button below and we\'ll send another validation email.';
  uuid = '';

  constructor(private titleService: Title, public dialog: MatDialog, private route: ActivatedRoute, public router: Router, private spinnerService: Ng4LoadingSpinnerService, public resendValidationLinkService: ResendValidationLinkService) { }

  ngOnInit() {
    this.titleService.setTitle('Resend Verification link');

    this.route.queryParams.subscribe(params => {
      if (params['uuid']) {
        this.uuid = params['uuid'];
      }
    });
  }

  resendValidationLink() {
    // this.serverError = '';

    if (this.uuid) {

      this.spinnerService.show();

      this.resendValidationLinkService.resendLink({ uuid: this.uuid }).subscribe(
        (res) => {

          this.spinnerService.hide();

          if (res.status === 200) {
            this.resetPasswordConfirmationDialog();

          } else {
            this.serverError = res.error;
          }
        }
      );
    }
  }

  resetPasswordConfirmationDialog(): void {

    const title = 'Password reset link sent!';
    const content = 'Please check your registered email to reset your password.';

    const dialogRef = this.dialog.open(SimpleDialogComponent, {
      width: '500px',
      autoFocus: false,
      data: { title: title, content: content }
    });

    dialogRef.afterClosed().subscribe(result => {
      this.router.navigate(['/login']);
    });
  }

}
