import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AppMaterialModule } from '../material/app-material.module';
import { SharedModule } from '../shared/shared.module';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { AuthRoutingModule } from './auth-routing.module';
import { ResendValidationLinkComponent } from './resend-validation-link/resend-validation-link.component';

@NgModule({
  imports: [
    CommonModule,
    AppMaterialModule,
    SharedModule,
    AuthRoutingModule,
  ],
  declarations: [LoginComponent, SignupComponent, ForgotPasswordComponent, ResetPasswordComponent, ResendValidationLinkComponent],
  entryComponents: [LoginComponent],
  exports: [ForgotPasswordComponent, ResetPasswordComponent],
})
export class AuthModule { }
