import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

import { AuthService } from '../../_service/auth.service';
import { SimpleDialogComponent } from '../../shared/simple-dialog/simple-dialog.component';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['../login/login.component.scss', './forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {

  hintText = 'Enter your email address below and we\'ll get you back on track.';
  serverError = '';

  forgotPasswordForm = new FormGroup({
    emailId: new FormControl('', [Validators.required, Validators.email])
  });

  validationMsg = {
    emailId: {
      required: 'Email cannot be left blank',
      email: 'Please enter a valid email ID'
    }
  };

  constructor(private titleService: Title, public dialog: MatDialog, private spinnerService: Ng4LoadingSpinnerService, public router: Router, public authService: AuthService) { }

  ngOnInit() {
    this.titleService.setTitle('Forgot Password!');
  }

  getErrorMessage(formControlName: string) {
    const formControl = this.forgotPasswordForm.get(formControlName);
    let message = '';

    if (formControl.errors) {
      Object.keys(formControl.errors).forEach(key => {
        message = this.validationMsg[formControlName][key];
      });
    }

    return message;
  }

  forgotPassword() {
    // this.serverError = '';

    const form = this.forgotPasswordForm;
    if (form.valid) {
      let emailId = form.get('emailId').value;
      emailId = emailId.trim();

      this.spinnerService.show();


      this.authService.forgotPassword(emailId).subscribe(
        (res) => {

          this.spinnerService.hide();

          if (res.status === 200) {
            this.resetPasswordConfirmationDialog();

          } else {
            this.serverError = res.error ? (res.error.message ? res.error.message : res.error) : 'Server error';
          }
        }
      );
    }
  }

  resetPasswordConfirmationDialog(): void {

    const title = 'Password reset link sent!';
    const content = 'Please check your registered email to reset your password.';

    const dialogRef = this.dialog.open(SimpleDialogComponent, {
      width: '500px',
      autoFocus: false,
      data: { title: title, content: content }
    });

    dialogRef.afterClosed().subscribe(result => {
      this.router.navigate(['/login']);
    });
  }

}
