import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { TranslateService } from '@ngx-translate/core';

import { environment } from '../../../environments/environment';
import { AuthService } from '../../_service/auth.service';
import { SimpleDialogComponent } from '../../shared/simple-dialog/simple-dialog.component';
import { TermsAndConditionsDialogComponent } from '../../shared/terms-and-conditions-dialog/terms-and-conditions-dialog.component';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['../login/login.component.scss', './signup.component.scss']
})
export class SignupComponent implements OnInit {
  oneCapsOneSpecialChar = environment.regex.oneCapsOneSpecialChar;
  atleastFewChars = environment.regex.atleastFewChars;
  signupForm = new FormGroup({
    userName: new FormControl('', [Validators.required, Validators.pattern(this.atleastFewChars), Validators.maxLength(30)]),
    companyName: new FormControl('', [Validators.required, Validators.pattern(this.atleastFewChars), Validators.maxLength(30)]),
    emailId: new FormControl('', [Validators.required, Validators.email, Validators.maxLength(30)]),
    password: new FormControl('', [
      Validators.required,
      Validators.pattern(this.oneCapsOneSpecialChar),
      Validators.minLength(8),
      Validators.maxLength(15)
    ]
    ),
    termsConditionsAgreement: new FormControl(true, [Validators.requiredTrue])
  });

  validationMsg = {
    userName: {
      required: 'Name cannot be left blank',
      maxlength: 'Name should be of at most 30 characters'
    },
    companyName: {
      required: 'Company cannot be left blank',
      maxlength: 'Company should be of at most 30 characters'
    },
    emailId: {
      required: 'Email cannot be left blank',
      email: 'Please enter a valid email ID',
      maxlength: 'Email should be of at most 30 characters'
    },
    password: {
      required: 'Password cannot be left blank',
      pattern: 'Password should have least 1 character in caps and 1 special character',
      minlength: 'Password should be of at least 8 characters',
      maxlength: 'Password should be of at most 15 characters'
    },
    confirmPassword: {
      required: 'Confirm Password cannot be left blank',
      pattern: 'Confirm Password should have least 1 special character and 1 character in Caps',
      minlength: 'Confirm Password should be of at least 8 characters',
      maxlength: 'Confirm Password should be of at most 15 characters',
      notequal: 'Passwords do not match'
    },
    termsConditionsAgreement: {
      required: 'Please accept terms &amp; conditions',
    }
  };

  passwordVisible = false;
  confirmPasswordVisible = false;

  passwordMinlengthError = true;
  passwordPatternError = true;

  serverError: any;

  constructor(private titleService: Title, public dialog: MatDialog, public router: Router, private translate: TranslateService, private spinnerService: Ng4LoadingSpinnerService, public authService: AuthService) { }

  ngOnInit() {
    this.titleService.setTitle('Get started!');

    this.signupForm.addControl('confirmPassword', new FormControl('', [
      Validators.required,
      Validators.pattern(this.oneCapsOneSpecialChar),
      Validators.minLength(8),
      Validators.maxLength(15),
      this.validateAreEqual.bind(this)
    ]));

    const passwordCtrl = this.signupForm.get('password');
    passwordCtrl.valueChanges.subscribe(() => {

      const errors = passwordCtrl.errors;
      const dirty = passwordCtrl.dirty;
      const status = passwordCtrl.status;

      if (errors) {
        if (errors['required']) {
          this.passwordMinlengthError = this.passwordPatternError = true;
        } else {
          this.passwordMinlengthError = errors['minlength'] ? true : false;
          this.passwordPatternError = errors['pattern'] ? true : false;
        }
      } else if (dirty && status === 'VALID') {
        this.passwordMinlengthError = this.passwordPatternError = false;
      }
    });
  }

  private validateAreEqual(fieldControl: FormControl) {
    return fieldControl.value === this.signupForm.get('password').value ? null : {
      notequal: true
    };
  }
  onKey(event) {
    const checkbox = this.signupForm.get('termsConditionsAgreement');
    const checkboxValue = checkbox.value;
    if (event.keyCode === 13) {
      if (checkboxValue === true) {
        checkbox.reset();
      } else {
        checkbox.setValue(true);
      }
    }
  }

  getErrorMessage(formControlName: string) {
    const formControl = this.signupForm.get(formControlName);
    let message = '';

    if (formControl.errors) {
      Object.keys(formControl.errors).forEach(key => {
        message = this.validationMsg[formControlName][key];

        if (formControlName === 'password' || formControlName === 'confirmPassword') {
          if (key === 'minlength' || key === 'pattern') {
            message = '';
          }
        }
      });
    }

    return message;
  }

  showPassword() {
    this.passwordVisible = true;
  }

  hidePassword() {
    this.passwordVisible = false;
  }

  showConfirmPassword() {
    this.confirmPasswordVisible = true;
  }

  hideConfirmPassword() {
    this.confirmPasswordVisible = false;
  }

  openTermsConditionsDialog() {
    const dialogRef = this.dialog.open(TermsAndConditionsDialogComponent, {
      width: '870px',
      height: '485px',
      disableClose: true
    });

    dialogRef.componentInstance.termsConditions = this.signupForm.get('termsConditionsAgreement').value;

    dialogRef.afterClosed().subscribe(result => {
      this.signupForm.get('termsConditionsAgreement').setValue(result);
    });
  }

  signup() {
    this.serverError = '';
    const form = this.signupForm;

    if (form.valid) {
      let userName = form.get('userName').value;
      userName = userName.trim();

      let companyName = form.get('companyName').value;
      companyName = companyName.trim();

      let emailId = form.get('emailId').value;
      emailId = emailId.trim();

      const password = form.get('password').value;
      const confirmPassword = form.get('confirmPassword').value;

      const termsConditionsAgreement = form.get('termsConditionsAgreement').value;

      const requestData = {
        'userFullName': userName,
        'company': companyName,
        'email': emailId,
        'password': password,
        'confirmedPassword': confirmPassword,
        'agreeToTerms': termsConditionsAgreement
      };

      this.spinnerService.show();

      this.authService.signup(requestData).subscribe(
        (res) => {

          this.spinnerService.hide();

          if (res.status === 200) {
            this.signupConfirmationDialog();

          } else {
            this.serverError = res.error.message;
          }
        }
      );
    }
  }

  signupConfirmationDialog(): void {

    const title = 'Thanks for signing-up!';
    const content = 'Please check your registered email to verify your account.';

    const dialogRef = this.dialog.open(SimpleDialogComponent, {
      width: '500px',
      autoFocus: false,
      data: { title: title, content: content }
    });

    dialogRef.afterClosed().subscribe(result => {
      this.router.navigate(['/login']);
    });
  }
}
