import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { environment } from '../../environments/environment';
import { HandleErrorService } from './handle-error.service';

@Injectable({
  providedIn: 'root'
})
export class ResendValidationLinkService {
  resendValidationUrl = environment.authServer + '/api/auth/resendLink/submit';

  constructor(private httpClient: HttpClient, private handleErrorService: HandleErrorService) { }

  resendLink(requestData): Observable<any> {

    const body = new URLSearchParams();
    Object.keys(requestData).forEach(key => {
      body.set(key, requestData[key]);
    });
    body.set('clientType', 'web');

    const httpOptions: { headers; observe; } = {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'
      }),
      observe: 'response'
    };

    return this.httpClient.post<any>(this.resendValidationUrl, body.toString(), httpOptions)
      .pipe(
        tap(res => {
          console.log(res);
        }),
        catchError(this.handleErrorService.handleError('resend Validation link', []))
      );
  }
}
