import { Injectable } from '@angular/core';
import { of } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';

import { environment } from '../../environments/environment';
import { HandleErrorService } from './handle-error.service';

@Injectable({
  providedIn: 'root'
})
export class LogoService {
  logoListUrl = environment.contentServer + '/api/contentMgmt/getActiveLogoList';
  logoList: any;

  constructor(private httpClient: HttpClient, private handleErrorService: HandleErrorService) { }

  getActiveLogoList() {
    if (this.logoList && this.logoList.length) {
      return of(this.logoList);
    } else {
      return this.httpClient.get<any>(this.logoListUrl, { observe: 'response' })
        .pipe(
          map(res => {
            if (res['body'].logoList) {
              this.logoList = [];
              res['body'].logoList.forEach(url => {
                if (
                  url.indexOf('.jpg') !== -1 ||
                  url.indexOf('.JPG') !== -1 ||
                  url.indexOf('.png') !== -1 ||
                  url.indexOf('.PNG') !== -1
                ) {
                  this.logoList.push(environment.contentServer + '/api/contentMgmt/getLogoImage?logoFileName=' + url);
                }
              });
            }
            return this.logoList;
          }),
          catchError(this.handleErrorService.handleError('getLogoImage', []))
        );
    }
  }
}
