import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { ResendValidationLinkComponent } from './resend-validation-link/resend-validation-link.component';

const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full', runGuardsAndResolvers: 'always' },
  { path: 'login', component: LoginComponent, runGuardsAndResolvers: 'always' },
  { path: 'signup', component: SignupComponent, runGuardsAndResolvers: 'always' },
  { path: 'forgot-password', component: ForgotPasswordComponent, runGuardsAndResolvers: 'always' },
  { path: 'reset-password', component: ResetPasswordComponent, runGuardsAndResolvers: 'always' },
  { path: 'resend-validation-link', component: ResendValidationLinkComponent, runGuardsAndResolvers: 'always' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule { }
