import { Component, OnInit } from '@angular/core';
import { ElementRef, Renderer2, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { DragScrollComponent } from 'ngx-drag-scroll';
import { DomSanitizer } from '@angular/platform-browser';

import { AuthService } from '../../_service/auth.service';
import { LogoService } from '../../_service/logo.service';
import { TestimonialVideoService } from '../../_service/testimonial-video.service';
import { ViewTestimonialVideoComponent } from '../../shared/view-testimonial-video/view-testimonial-video.component';
import { ViewPartnerLogosComponent } from '../../shared/view-partner-logos/view-partner-logos.component';

@Component({
  selector: 'app-login-sidebar',
  templateUrl: './login-sidebar.component.html',
  styleUrls: ['./login-sidebar.component.scss', './ngx-drag-scroll.scss']
})
export class LoginSidebarComponent implements OnInit {

  videoUrl: any;
  leftNavDisabled = false;
  rightNavDisabled = false;

  logoUrls = [];

  @ViewChild('nav', { read: DragScrollComponent }) ds: DragScrollComponent;

  constructor(sanitizer: DomSanitizer, private element: ElementRef, public dialog: MatDialog, private authService: AuthService, private testimonialVideoService: TestimonialVideoService, private logoService: LogoService) { }

  ngOnInit() {
    this.testimonialVideoService.getTestimonialVideoUrl().subscribe(
      (res) => {
        if (!res.error) {
          this.videoUrl = res;
        } else {
          this.videoUrl = '';
        }
      }
    );

    this.logoService.getActiveLogoList().subscribe(
      (res) => {
        if (!res.error) {
          this.logoUrls = res;
        } else {
          this.logoUrls = [];
        }
      }
    );
  }

  openVideoDialog(): void {
    const dialogRef = this.dialog.open(ViewTestimonialVideoComponent, {
      disableClose: true,
    });
    dialogRef.componentInstance.videoUrl = this.videoUrl;
  }

  openPartnerLogoDialog(): void {
    const dialogRef = this.dialog.open(ViewPartnerLogosComponent, {
      width: '588px',
      disableClose: true,
      data: { logoUrls: this.logoUrls }
    });
  }

  moveLeft() {
    this.ds.moveLeft();
  }

  moveRight() {
    this.ds.moveRight();
  }

  leftBoundStat(reachesLeftBound: boolean) {
    this.leftNavDisabled = reachesLeftBound;
  }

  rightBoundStat(reachesRightBound: boolean) {
    this.rightNavDisabled = reachesRightBound;
  }
}
